import React, { useState } from "react";
import { useInView } from "react-intersection-observer";
import "./todos.scss";

const Item = ({ props }) => {
  const [com, setCom] = useState();
  const { ref, inView } = useInView({
    //  
    threshold: 0,
    // triggerOnce: true,
    fallbackInView: true,
    // rootMargin: "-100px 0px",
    placeholder: 0.3,
  });

  const onChange = () => {};

  return (
    <div ref={ref} key={props.key} className="items">
      {inView && (
        <>
          <span>{props.title}</span>
          <button></button>
          <label htmlFor={props.key}>
            <input type="checkbox" name="" id={props.key} />
            
          </label>
        </>
      )}
    </div>
  );
};

export default Item;
